.comment {
  font-family: Arial, sans-serif;
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background-color: #f5f5f5;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.comment-table {
  width: 100%;
  border-collapse: collapse;
}

.comment-table thead th {
  text-align: center;
  background-color: #333;
  color: #fff;
  position: sticky;
  top: 0;
  z-index: 1;
}

.comment-table tbody td {
  text-align: center;
  border-bottom: 1px solid #ddd;
}

.comment-table th {
  background-color: #333;
  color: #fff;
}

.page {
  text-align: center;
  margin-top: 20px;
}

.page a {
  font-size: 16px;
  margin: 0 5px;
  color: #333;
  text-decoration: none;
  padding: 5px 10px;
  border-radius: 3px;
  transition: background-color 0.3s ease;
}

.page a:hover {
  background-color: #333;
  color: #fff;
}

.comment-form {
  margin-top: 20px;
}

.comment-form input[type='text'] {
  width: 200px;
  height: 40px;
  font-size: 16px;
  padding: 0 10px;
  border: 1px solid #ddd;
  border-radius: 3px;
  margin-right: 10px;
}

.comment-form textarea {
  width: 100%;
  height: 100px;
  font-size: 16px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 3px;
  resize: vertical;
}

/* COURSE 게시판 스타일 */
.board_table {
	width: 100%;
	border-collapse: collapse;
}


.board_table th {
	padding: 10px;
	border: 1px solid #ddd;
	text-align: center;
	background-color: #f2f2f2;
}

.board_table td {
	padding: 8px;
	border: 1px solid #ddd;
  text-align: center
}

.board_table a {
	color: #333;
	text-decoration: none;
}

.board_table a:hover {
	text-decoration: underline;
}

.layout_padding {
	padding: 35px 0;
}

.layout_padding2 {
	padding: 45px 0;
}

.layout_padding2-top {
	padding-top: 45px;
}

.layout_padding2-bottom {
	padding-bottom: 45px;
}

.layout_padding-top {
	padding-top: 90px;
}

.layout_padding-bottom {
	padding-bottom: 90px;
}

.btn_course {
	font-size: 14px;
	background-color: #f2f2f2; /* 테이블 헤더와 동일한 배경색 */
	color: #333; /* 테이블 링크와 동일한 글자색 */
	border: 1px solid #ddd; /* 테이블 셀과 동일한 테두리 */
	padding: 4px 12px; /* 내부 여백 */
	text-align: center; /* 글자 가운데 정렬 */
	text-decoration: none; /* 글자 밑줄 없음 */
	display: inline-block; /* 인라인 블록 요소로 표시 */
	cursor: pointer; /* 마우스 오버 시 커서 변경 */
	transition: background-color 0.3s, color 0.3s; /* 부드러운 색상 전환 효과 */
}

.btn_course:hover {
	background-color: #ddd; /* 마우스 오버 시 배경색 변경 */
}

.button-container {
	display: flex;
	justify-content: flex-end;
	margin-left: auto; /* 오른쪽으로 이동시킵니다. */
}




/* 게시판 스타일 끝 */